import React, { useState } from 'react';
import Login from './Login';
import App from '../../App';
import InfoAcceso from '../../Datos/InfoAcceso';
// import './AppLogin.css';
import Loader from './Loader';

const AppLogin = () => {
  const [username, setUsername] = useState('');
  const [password, setPassword] = useState('');
  const [isAuthenticated, setIsAuthenticated] = useState(false);
  const [isLoading, setIsLoading] = useState(false); // Estado para el loader

  const handleLogin = (e) => {
    // e.preventDefault();
    setIsLoading(true); // Activa el loader
    
    // Simula un retraso para la validacin (por ejemplo, simulacin de llamada a API)
    setTimeout(() => {
        // Buscar el usuario en el arreglo de usuarios
        const user = InfoAcceso.find(
          (user) => user.username === username && user.password === password
        );
  
        if (user) {
          setIsAuthenticated(true); // Autenticacin exitosa
        } else {
          alert('Usuario o contrasea incorrectos');
        }
  
        setIsLoading(false); // Desactiva el loader
      }, 2000); // Retraso de 2 segundos
    };

    const handleLogout = () => {
      // Aqu actualizas el estado de autenticacin a 'false' cuando el usuario cierre sesin
      setIsAuthenticated(false);
      // Tambin puedes limpiar cualquier dato de sesin aqu si es necesario, por ejemplo:
      localStorage.removeItem('authToken');
    };

  // Si est autenticado, muestra el contenido principal; si no, muestra el inicio de sesin.
  return (
    <div>
      {isAuthenticated ? (
        <App onLogout={handleLogout} estiloApp={"estilo-app"}/>
      ) : isLoading ? (
        <Loader text={`Validando credenciales...`}/>
      ) : (
        <Login 
          onLogin={handleLogin} 
          setUsername={setUsername} 
          setPassword={setPassword} 
          estiloLogin={"estilo-login"}
        />
      )}
    </div>
  );
};

export default AppLogin;
